import Vue from 'vue';
import Router from 'vue-router';
import HelloWorld from '@/components/HelloWorld';
import SimpleCalculator from '@/components/SimpleCalculator.vue';
import AutocSelect from '@/components/autoc-select/AutocSelect.vue';
import Floater from '@/components/floater/Floater.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    }, {
      path: '/simple-calculator',
      name: 'SimpleCalculator',
      component: SimpleCalculator
    }, {
      path: '/autoc-select',
      name: 'some kind of select',
      component: AutocSelect,
      props: {
        options: [{
          name: 'tomate'
        }, {
          name: 'cebolla'
        }, {
          name: 'queso crema'
        }, {
          name: 'queso criollo'
        }, {
          name: 'queso cheddar'
        }, {
          name: 'queso mozzarella'
        }]
      }
    }, {
      path: '/floater',
      name: 'some kind of floater',
      component: Floater,
      props: {
        toRender: {
          fileName: '@/components/autoc-select/AutocSelect.vue',
          component: AutocSelect,
          props: {
            options: [{
              name: 'all'
            }, {
              name: 'your base'
            }, {
              name: 'are'
            }, {
              name: 'belong'
            }, {
              name: 'to'
            }, {
              name: 'us'
            }]
          }
        }
      }
    }
  ]
});



