import firebase from './firebase.js';
// import eventBus from './event-bus.js';
import nutrilib from '@/nutrilib/nutrilib.js';

var db = firebase.database();
var reagents = [];
var reagent = '';

for (reagent in nutrilib.nutrient) {
  reagents.push({ name: reagent });
}

// db.ref('root/reagent').on('value', function (snapshot) {
//   reagents = snapshot.val();
//   eventBus.$emit('reagents-list-changed', reagents);
// });

export default {
  // ./.reagent.schema.json
  setReagent (reagent) {
    db.ref('root/reagent/' + reagent.name).set(reagent.nutritionFacts);
  },

  // ./.recipe.scheme.json
  setRecipe (recipe) {
    db.ref('root/recipe/' + recipe.name).set(recipe.reagents);
  },

  knowsReagent (reagentName) {
    var reagent;
    for (reagent in nutrilib.nutrient) {
      if (reagent === reagentName) {
        return true;
      }
    }

    return false;
  },

  reagents

  // /* i don't know when will firebase deliver the reagents list,
  //  * so i am sending you a promise that firebase will deliver
  //  * the reagents list instead
  //  * use it as: db.promiseOfReagents().then(function (snapshot) {
  //  *   the value is at snapshot.val()...
  //  * });
  //  */
  // promiseOfReagents () {
  //   return db.ref('root/reagent').once('value');
  // }
};



