import db from '@/db.js';
import AutocSelect from '@/components/autoc-select/AutocSelect.vue';
import eventBus from '@/event-bus.js';

export default {
  props: ['model', 'index', 'blank'],

  mounted () {
    this.reagents = db.reagents;
  },

  data () {
    return {
      handleEdit: function () {
        this.$emit('edit');
      },

      setFocus: function (name) {
        this.$refs[name].focus();
      },

      handleFocus: function (name) {
        this.focused = name;
        this.hasFocus = true;
      },

      handleLostFocus: function () {
        this.hasFocus = false;
      },

      focused: '',
      hasFocus: false,
      reagents: [],
      blankData: {
        name: '',
        amount: ''
      }
    };
  },

  methods: {
    reportDeletion () {
      this.$emit('deletion', this.index);
      this.$emit('edit');
    },

    knowsReagent () {
      return db.knowsReagent(this.model.name);
    }
  },

  components: {
    'autoc-select': AutocSelect
  }
};



