import Recipe from './Recipe.vue';
import eventBus from '@/event-bus.js';
import nutrilib from '@/nutrilib/nutrilib.js';

var recipe;

export default {
  name: 'simple-calculator',

  data () {
    return {
      theHorror: {},
      missing: {},

      translate: {
        es: {
          carbs: 'carbohidratos',
          proteins: 'proteínas',
          calories: 'calorías'
        }
      }
    };
  },

  mounted () {
    recipe = this.$refs.recipe;
    this.updateNutrients();
  },

  methods: {
    updateNutrients () {
      var missingReagent;
      this.theHorror = nutrilib.showMeTheHorror(recipe.prettyReagents());
      this.missing = {};

      for (missingReagent in this.theHorror.missingNutrients) {
        this.missing[missingReagent] = {
          carbs: '',
          calories: '',
          proteins: ''
        };
      }
    },

    addNutrient (nut, reagent) {
      for (var i in nut) {
        nut[i] = eval(nut[i]);
      }

      nutrilib.nutrient[reagent] = nut;
      this.updateNutrients();
    }
  },

  components: {
    'recipe': Recipe
  }
};



