import Reagent from './Reagent.vue';
import firebase from '@/db.js';
import vSelect from 'vue-select';
import nutrilib from '@/nutrilib/nutrilib.js';

export default {
  props: ['allReagents'],

  data () {
    return {
      reagents: [{
        name: '',
        amount: 0
      }],
      name: '',
      buddies: 1
    };
  },

  methods: {
    handleEdit () {
      this.$emit('edit');
    },
    
    prettyReagents () {
      var pretty = {};
      var reagent;

      for (reagent in this.reagents) {
        reagent = this.reagents[reagent];

        pretty[reagent.name] =
          pretty[reagent.name] || 0;
        pretty[reagent.name] += eval(reagent.amount);
      }

      return pretty;
    },
    
    addBlankReagent () {
      console.log(this.reagents);
      console.log(nutrilib.showMeTheHorror(this.reagents));

      this.reagents.push({
        name: '',
        amount: ''
      });
    },

    deleteReagent (index) {
      console.log(index + '!!!!!');
      this.reagents.splice(index, 1);
    }
  },

  components: {
    'reagent': Reagent,
    'v-select': vSelect
  }
};



