/*
 * options="list of items that will modeled by the autoc-select"
 * @addition: fires when a new item is added to the list
 * @selection: fires when the selected item has changed
 * TODO: interact with firebase
 */
import AutocOption from './AutocOption.vue';
import Floater from '@/components/floater/Floater.vue';
import db from '@/db.js';

export default {
  props: ['options', 'value'],

  computed: {
    query: {
      get () {
        return this.value;
      },

      set (newVal)  {
        this.$emit('input', newVal);
      }
    }
  },

  data () {
    function isQueryOnSuggestions () {
      return this.suggestions.filter((i) => {
        return i.name.toLowerCase() === this.query.toLowerCase();
      }).length > 0;
    }

    function isQueryOnOptions () {
      return this.options.filter((i) => {
        return i.name.toLowerCase() === this.query.toLowerCase();
      }).length > 0;
    }

    return {
      suggestions: [],
      selected: {},
      highlighted: 0,

      isQueryOnSuggestions: isQueryOnSuggestions,

      shouldSuggestAddition () {
        return this.query.length > 0 && (
          !isQueryOnOptions.call(this)
        );
      },

      notifyKeyDown ($event) {
        this.$emit('keydown', $event);
      },

      showSuggestions () {
        return this.query.length > 0 &&
          !isQueryOnOptions.call(this);
      },

      isCreateNewHighlighted () {
        return this.suggestions.length === this.highlighted;
      },

      setSelection (selected) {
        this.$emit('selection', this.suggestions[this.highlighted].name);

        this.selected = selected;
        this.query = this.selected.name;

        this.suggestions = [];
        this.highlighted = 0;
      },

      changeHighlighted (step) {
        this.highlighted = (this.highlighted+step) %
          (this.suggestions.length+1);

        if (this.highlighted < 0) {
          this.highlighted += this.suggestions.length+1;
        }
      },

      onInput () {
        this.suggestions = [];

        if (this.query !== '') {
          for (var option in this.options) {
            if (this.options[option].name.toLowerCase().
                indexOf(this.query.toLowerCase()) > -1) {
              this.suggestions.push(this.options[option]);
            }
          }
        }

        if (this.isQueryOnSuggestions()) {
          this.$emit('selection', this.selected,
                    this.suggestions[this.highlighted].name);
        }
      },

      confirmSelection () {
        if (!this.showSuggestions()) {
          return;
        }

        if (this.suggestions.length === 0 || this.isCreateNewHighlighted()) {
          this.addQuery();
        } else {
          this.setSelection(this.suggestions[this.highlighted]);
        }
        this.highlighted = 0;
      },

      addQuery () {
        var added = {
          name: this.query
        };
        this.options.push(added);
        this.setSelection(added);
        this.suggestions.push(added);

        this.$emit('addition', added);
      },

      updateQuery () {
        this.$emit('input', this.$refs.query.value);
        this.$emit('edit');
      }
    };
  },

  components: {
    'autoc-option': AutocOption,
    'floater': Floater
  }
};



